.main {
    height: 700px;
    background: rgb(25, 167, 163);
    background: radial-gradient(
      circle,
      rgba(25, 167, 163, 1) 0%,
      rgba(72, 95, 201, 1) 100%,
      rgba(33, 114, 223, 1) 100%
    );
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:flex-start;
  padding-top:100px;
 
}
.input>input{
    height: 28px;
    width: 180px;
    border-radius:2px;
    border: none;
    outline:none;
    padding-top: 3px;
    
   
}
.todo{
    /* width: ; */
   
}
::placeholder{
    font-size: 15px;
    font-weight: 600;
    color:rgba(0, 0, 0, 0.61);
    padding: 10px;
}
.input>button{
    height: 34px;
    width: 70px;
    border: none;
    background-color:rgba(0, 0, 0, 0.61);
    border-radius: 5px;
    color: white;
    font-size: 20px;
}
.todo>div:nth-child(1){
    text-align: center;
    font-size:25px;
    color: white;
    font-weight: 600;
    position: relative;
    top: -80px;
}
