.nav{
      
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
   
    padding: 30px;
    font-weight: 600;
    color: blue;
    font-size: 30px;
    
 
}
.link{
    text-decoration: none;
   
}
.total{
